.App {
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.22,0.61,0.36,1);
}

.vignette {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: radial-gradient(circle, transparent 50%, rgba(0, 0, 0, 0.16) 150%);
  pointer-events: none;
  transition: opacity 0.64s cubic-bezier(0.65,0.05,0.36,1);
  z-index: 10;
}

.vignette.hidden {
  opacity: 0;
}
