html {
  width: 100%;
  max-width: 100vw;
  height: 100%;
  font-size: 20px;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}

body {
  width: 100%;
  max-width: 100vw;
  height: 100%;
  overflow: hidden;
}

#root {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
}

.svgContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.svgContainer > svg {
  width: 100%;
  height: auto;
}

path, g, circle {
  transform-origin: 50% 50%;
}

@media screen and (min-width: 1660px) {
  html {
    font-size: 21px;
  }
}
