.eyeball {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 0;
  animation: appearEye 0.15s cubic-bezier(0.65,0.05,0.36,1) forwards;
}

.eyeflap {
  background-color: #10493A;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  z-index: 3;
  animation: openEye 0.64s cubic-bezier(0.65,0.05,0.36,1) forwards;
}

.eyelid {
  position: absolute;
  width: 100%;
  z-index: 2;
  animation: blink 2.3s infinite;
  transition: transform 0.4s cubic-bezier(0.65,0.05,0.36,1);
}

.eyelid.closed {
  transform: scaleY(0.05);
}

.eyelid::before, .eyelid::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  background-color: #10493A;
}

.eyelid::before {
  top: -1000%;
  bottom: 90%;
}

.eyelid::after {
  top: 90%;
  bottom: -1000%;
}

.iris {
  position: absolute;
  width: 23%;
  animation: squeeze 2.3s infinite;
  z-index: 1;
}

.irisContainer {
  width: 100%;
  height: 100%;
}

.irisController {
  width: 100%;
  height: 100%;
}

.sclera {
  height: 90%;
  width: 90%;
  background-color: #FFFAD6;
  border-radius: 50%;
  animation: squeeze 2.3s infinite;
}

@keyframes appearEye {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes openEye {
  0% {
    transform: none;
  }
  100% {
    transform: translateY(-100%);
  }
}

@keyframes blink {
  90% {
    transform: none;
    animation-timing-function: ease-in;
  }
  93% {
    transform: translateY(30px) scaleY(0.05);
  }
  100% {
    animation-timing-function: ease-out;
  }
}

@keyframes squeeze {
  90% {
    transform: none;
    animation-timing-function: ease-in;
  }
  92% {
    opacity: 1;
  }
  93% {
    opacity: 0;
    transform: translateY(3px) scaleY(0.8);
  }
  94% {
    opacity: 1;
  }
  100% {
    animation-timing-function: ease-out;
  }
}
